//------------------------------------------------------------------------
//nav
//------------------------------------------------------------------------

.nav{
max-width: 100px;
li{
    width: 100%;
    &.impact {
        path{
            fill: $primary;
        }
        rect{
            fill: transparent;
        }
        &:hover, &:active, &:focus{
            path{
                fill: $edr-mauve;
            }
            rect{
                fill: $edr-mauve;
            }
        }
    }
    &.journey{
        path.outer{
            fill: transparent;
        }
        path.inner{
            fill: $primary;
        }
        &:hover, &:active, &:focus{
            path.outer{
                fill: $edr-orange;
            }
            path.inner{
                fill: $edr-orange;
            }
        }
    }
    &.future{
        path.outer{
            fill: transparent;
        }
        path.inner{
            fill: $primary;
        }
        &:hover, &:active, &:focus{
            path.outer{
                fill: $edr-green;
            }
            path.inner{
                fill: $edr-green;
            }
        }
    }
}
}

        


//------------------------------------------------------------------------
//hero
//------------------------------------------------------------------------

//------------------------------------------------------------------------
//headers
//------------------------------------------------------------------------


.section-header{
    .gradient-mask{
        width: 100%;
        height: 100%;
        background-position: center top;
        background-size: cover;
        background-repeat: no-repeat;
        min-height: 415px;
        display: block;
        mask-image: linear-gradient(to right, transparent, black);
    } 
}

.heading-arrow{
    path{
        fill: $edr-mauve;
    }
}

#impact.section-header{
    .gradient-mask{
        background-image: url("../img/photos/bkg-impact.png");
    }
    .shape-container{
        rect{
            fill: $edr-mauve;
        }
    }
}

#journey.section-header{
    .gradient-mask{
        background-image: url("../img/photos/bkg-journey.jpg");
    }
    .shape-container{
        rect{
            fill: $edr-orange;
        }
    }
}

#future.section-header{
    .gradient-mask{
        background-image: url("../img/photos/bkg-future.jpg");
    }
    .shape-container{
        rect{
            fill: $edr-green;
        }
    }
}

//------------------------------------------------------------------------
//layout modules
//------------------------------------------------------------------------

// stat grid //

.stats-grid{
    
    *{
        overflow: visible;
        font-weight: bold;
    }
    .col-sm{
    background: none;
        &:hover, &:active, &:focus{
            background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" height="273.345" viewBox="0 0 273.345 273.345"><rect id="ui-square-mauve" width="201.073" height="201.073" transform="matrix(0.875, 0.485, -0.485, 0.875, 97.482, 0)" fill="%23501141"/></svg>');
            background-repeat: no-repeat;
            overflow: visible;
            background-position: center center;
            color: white;
        }
    }
}

figure.stat {
    max-width: 200px;
}

// map and legend //

.legend{
        #use-edreports{
            fill: $primary;
        }
        #does-not-use-edreports{
            fill: $primary-soft;
        }
    }
    //------------------------------------------------------------------------
//TYPOGRAPHY
//------------------------------------------------------------------------
.chart-label, 
.nav-label{
text-transform: uppercase;
letter-spacing: 1px;
}
