.accordion {
    .icon {
        margin-right: 1rem;
        background-size: contain;
        background-position: center center;
        height: 30px;
        width: 30px;
        display: block;
        background-repeat: no-repeat;
    }
    .innovation {
        .icon {
            background-image: $accordion-icon-star-primary;
