//------------------------------------------------------------------------
//BUTTONS
//------------------------------------------------------------------------

.button-shape-container{
    margin-top: -10px;
    .btn-caret{
        position: relative;
        margin-left: -50%;
        z-index: 5;
    }
    .btn-shape{
        fill: white;
        position: relative;
        z-index: 1;
    }
    &:hover, &:focus{
        .btn-shape{
            fill: #0A3E59;
        }
    }
}
    .btn-light{
        background-color: white;
        color: $primary;
        svg,
        rect{
            fill: $primary;
            stroke: white;
        }
        path{
            stroke: white;
        }
        &:hover{
        background-color: $primary;
        color: white;
            svg,
            rect{
            fill: white;
            stroke: $primary;
            }
            path{
                stroke: $primary;
            }
        
        }
        &.mauve{
            svg,
        rect{
            fill: $edr-mauve;
            stroke: white;
        }
        }
    }
    .btn-dark{
        background-color: $primary;
        color: white;
        svg,
        rect{
            fill: white;
        }
        path{
            stroke: $primary;
        }
        &:hover{
            color: $primary;
            background-color: white;
            svg,
            rect{
            fill: $primary;
            }
        }
    }
    .btn-dark.mauve{
        &:hover{
            background-color: $edr-mauve;
            color: white;
            svg,
            rect{
                fill: white;
            }
            path{
                stroke: $edr-mauve;
            }
        }
    }
    .btn-light.orange{
        &:hover{
            background-color: $edr-orange;
            color: white;
            svg,
            rect{
                fill: white;
            }
            path{
                stroke: $edr-orange;
            }
        }
    }
    .btn-light.green{
        &:hover{
            background-color: $edr-green;
            color: white;
            svg,
            rect{
                fill: white;
            }
            path{
                stroke: $edr-green;
            }
        }
    }

//------------------------------------------------------------------------
//FIGCAPTION/BLCOKQUOTE
//------------------------------------------------------------------------
figcaption{
    &.blockquote-footer{
        &::before{
            content: none;
        }
    }
}
//------------------------------------------------------------------------
//CHARTS
//------------------------------------------------------------------------

.js-plotly-plot .plotly, .js-plotly-plot, .plotly div{
    font-family: $font-family-sans-serif !important;
    font-weight: 500;
}
//------------------------------------------------------------------------
//CHARTS
//------------------------------------------------------------------------
.background-shape {
    z-index: -1;
    width: 110vw;
    height: 110vw;
    position: fixed;
    // margin-left: -10vw;
}

@keyframes rotating {
    from {
        transform: rotate(0deg);
    }
    to { 
        transform: rotate(360deg);      
    }
}

.rotating {
    animation: rotating 300s linear infinite;
    &.medium-speed{
        animation-duration: 150s;
    }
    &.fast-speed{
        animation-duration: 75s;
    }
}
